body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  background: #0b0b0b;
  color: #ffffff;
}

nav {
  background: #000000;
  padding: 15px;
  text-align: center;
}

nav a {
  color: #4fc3f7;
  margin: 0 15px;
  text-decoration: none;
  font-weight: bold;
}

.container {
  max-width: 1100px;
  margin: auto;
  padding: 70px 20px;
}

.whatsapp {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: #25d366;
  color: #ffffff;
  padding: 14px 18px;
  border-radius: 50px;
  text-decoration: none;
  font-weight: bold;
}
